import React from 'react';
import {pw_type, pw_strength, combinations, entropy} from './Analyzer';
import {Guideline, expo, financial, checkString} from './Misc';
import {generatePassword} from './Generator';
import {issues} from './issues';
import {makeDict} from './dict';
import './App.css';
import './PassBar.css';
import './Pass.css';
import './Guidelines.css';
import {interpolateRgb, quantize} from "d3-interpolate";
import axios from 'axios';


const CPS = 588235;
//The dictionary lookup object
var dict = {};
var alphadict = {}
const Pass = props => (
  <tr className="passBox">
      <td>{props.name}</td>
      <td>{props.strength}</td>
      <td>{props.cracktime}</td>
      <td className="tablecell">
        <PassBar barStyle={props.barStyle} bits={props.bits}></PassBar>
      </td>
      <td>{props.issues}</td>
      <td>{props.rank}</td>
  </tr>
)


const InitPass = props => (
  <div className="passBox2">
    <p className="tablecell2">
      <PassBar barStyle={props.barStyle} bits={props.strength}></PassBar>
    </p>
    <p>{props.issues}</p>
  </div>
)

const PassBar = props => (
  <div className="TopBar">
    <div style={props.barStyle} className="LowerBar">
      {props.bits}
    </div>
  </div>
)

const Header = props => (
  <div>
    <h1>PassChecker</h1>
  </div>
)

const Title = props => (
  <tr className="passBox">
    <th>Password </th>
    <th>Strength </th>
    <th>Cracktime (sec)</th> 
    <th>Score</th>
    <th>Issues</th>
    <th>Rank</th>
  </tr>
)

const Generator = props => (
  <div>
    <h2>Generator</h2>
    <p>There are three types of generation provided:</p>
    <p>Generation by using random words.</p>
    <p>Generation by using random characters.</p>
    <p>Generation by using characters to create a passphrase.</p>
  </div>
)

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      inpass    : [],
      outpass   : [], 
      errors    : [],
      incurrent : "",
      outcurrent: "",
      select    : false,
      len       : 10,
      genBox    : ""
    }

    this.handleChangeLister = this.handleChangeLister.bind(this);
    this.handleClickLister = this.handleClickLister.bind(this);

    this.handleChangeGenerator = this.handleChangeGenerator.bind(this);
    this.handleClickGenerator = this.handleClickGenerator.bind(this);

    this.handleOutputChange = this.handleOutputChange.bind(this);
    this.handleChangeInputGenerator = this.handleChangeInputGenerator.bind(this);
  }

  componentDidMount() {
    // Get the dictionary
    axios.get("https://raw.githubusercontent.com/redbo/scrabble/master/dictionary.txt")
      .then(response => {
        let result = null;
        result = makeDict(response.data);
        dict = result.dict;
        alphadict = result.alphadict;
      })
      .catch(error => {
        console.log(error);
      })
  }

  render(){
    let inputStyle = {
      padding: "10px",
      outline: "none"
    }

    let flexStyle = {
      display: "flex",
      flexDirection: "row"
    }

    let flexCol = {
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
      overflow: "hidden"
    }
    
    const CurrPass = this.state.incurrent;
    var CurrentPass;
    if(CurrPass != null && CurrPass != "" && CurrPass != undefined){
      let barhue = {
        background: CurrPass.barStyle.background
      }
      CurrentPass = (
            <InitPass strength={CurrPass.strength} barStyle={barhue} bits={CurrPass.bits}></InitPass>
          )
    } else {
      CurrentPass = (
        null
      )
    }

    const GenList = this.state.outpass.map(pass => {
      return (
          <Pass key={pass.name} name={pass.name} strength={pass.strength} cracktime={pass.cracktime} barStyle={pass.barStyle} bits={pass.bits} rank={pass.rank} issues={pass.issues}></Pass>
      )
    })

    const select = this.state.select;
    var newGenerator;
    if(select){
      newGenerator = (
        <div>
          <Generator></Generator>
          <div style={flexCol}>
          <div>
              <div style={flexStyle}>
                <div>Length:</div>
                <input type="range" min="1" max="5" onChange={this.handleOutputChange} value={this.state.len}></input>
                <output id="barnum">{this.state.len} Word(s)</output>
              </div>
              <input id="outcurrent" type="text" onChange={this.hanldeChangeInputGenerator}></input>
              <button name="1"          onClick= {this.handleClickGenerator}>Generate</button>
              
          </div>
          <select onChange={this.handleChangeGenerator} id="genSelect" name="generators" size="3">
              <option value="1">Random Words</option>
              <option value="2">Random Characters</option>
              <option value="3">Passphrase</option>
          </select>
          </div>
          <table>
              <Title></Title>
              {GenList}
          </table>
        </div>
      )
    } else {
        newGenerator = (
          <div>
            <Generator></Generator>
            <div style={flexCol}>
              <div>
                <p>Length:</p>
                <input type="range" min="1" max="20" onChange={this.handleOutputChange} value={this.state.len}></input>
                <output id="barnum">{this.state.len} Character(s)</output>
              </div>
              <div>
                <button name="1" onClick= {this.handleClickGenerator}>Generate</button>
              </div>
              <select onChange={this.handleChangeGenerator} id="genSelect" name="generators" size="3">
                  <option value="1">Random Words</option>
                  <option value="2">Random Characters</option>
                  <option value="3">Passphrase</option>
              </select>
            </div>
            <table>
              <Title></Title>
              {GenList}
            </table>
          </div>
        )
    }

    const PassList = this.state.inpass.map(pass => {
      return (
          <Pass key={pass.name} name={pass.name} strength={pass.strength} cracktime={pass.cracktime} barStyle={pass.barStyle} bits={pass.bits} rank={pass.rank} issues={pass.issues}></Pass>
      )
    })

    return (
      <div className="App">
        <header className="App-header">
          <Header></Header>
          <h2>Compare</h2>
          <div style={flexStyle}>
            <input  id="inputPass" name="incurrent" onChange={this.handleChangeLister} type="text"></input>
            <button                name="4"         onClick={this.handleClickLister}> Add </button>
          </div>
          <div style={inputStyle}>
            {CurrentPass}
          </div>
          <table>
            <Title></Title>
            {PassList}    
          </table> 
          {newGenerator}
          <Guideline></Guideline>
        </header>
      </div>
    );
  }

  handleChangeLister(e) {
    console.log("Handling change. (Lister) ")
    var val = e.target.value;
    if(val.length > 20){
      val = val.substring(0, 20);
      e.target.value = val;
    }
    const value = analyzePassword(val);
    this.setState({
      ...this.state,
      [e.target.name]: value
    });
  }

  handleClickLister(e){
    console.log("Handling click. (Lister)");
    if(this.state.incurrent != "" && this.state.incurrent != null){
      let temp = [...this.state.inpass, this.state.incurrent];
      temp.sort(comparePass);
      let i = 1;
      temp.forEach(function (element) {
        element.rank = i;
        i++;
      });
      this.setState({
          ...this.state,
          inpass: temp,
          incurrent: null
      });
    }
    let input = document.getElementById("inputPass");
    input.value = "";
  }

  handleOutputChange(e){
    this.setState({
      ...this.state,
      len: e.target.value
  });
  }

  handleChangeInputGenerator(e){
    if(e.value.length > this.state.len){
      e.value = e.value.substring(0, this.state.len)
    }
  }

  handleChangeGenerator(){
    console.log("Handling change. (Generator) ")
    let e = document.getElementById("genSelect");
    if(e.value == "3"){
      this.setState({
        ...this.state,
        select: true,
        len: 3
      });

    } else {
      this.setState({
        ...this.state,
        select: false,
        len: 10
      });
    }
  }

  handleClickGenerator(){
    console.log("Handling click. (Generator)");
    let e = document.getElementById("genSelect");
    let input = document.getElementById("outcurrent")
    let pass = "";
    switch(e.value){
      case "1":
        pass = generatePassword(1, this.state.len, dict, null);
        break;
      case "2":
        pass = generatePassword(2, this.state.len, dict, null);
        break;
      case "3":
        if(/^[a-z]+$/i.test(input.value)){
          pass = generatePassword(3, this.state.len, alphadict, input.value);
        } else {
          console.log("ERROR, ONLY LETTERS ALLOWED")
        }
        break;
      default:
        console.log("Case not found.")
        break;
    }
    let val = pass.toLowerCase();
    if(val.length > 20){
      val = val.substring(0, 20);
    }
    const value = analyzePassword(val);
    if(value != undefined){
      this.setState({
        ...this.state,
        outcurrent: "",
        outpass: [...this.state.outpass, value]
      });
    }
  }


}

// Sort passwords base on length
function comparePass(a, b){
  console.log(typeof(a.bits))
  if(Number(a.bits) < Number(b.bits)){
    return 1;
  }
  if(Number(a.bits) > Number(b.bits)){
    return -1;
  }
  return 0;
}

// Analyze Password
function analyzePassword(e){
  if(e != null){
    let text = e;
    if(checkString(text)){
      let charpool = pw_type(text);
      let length = text.length;
      let bits = entropy(length, charpool.amount);
      let comb = combinations(length, charpool.amount);
      let crack = cracktime(comb)
      crack = expo(crack, 2);
      let obj = pw_strength(bits);
      let style = addPassword(obj.value);
      bits = financial(bits);
      let issue = issues(text, charpool.types, length, dict);
      console.log(issue)
      return {name: text, strength: obj.strength, cracktime: crack, bits:bits ,barStyle: style};
    }
  }
}

// Password Hue
function passHue(value){
  let colors = quantize(interpolateRgb("red", "green"), 5);
  return colors[value]
}

// Passbar style
function addPassword(value){
  let percentage = 25*(value+1);
  let hue = passHue(value);
  let style = {
    width: percentage + "%",
    background: hue
  }
  return style;
}

// Amount of time in seconds until crack.
function cracktime(comb){
  return comb/CPS;
}



export default App;
